import styled from "styled-components";

const Title = styled.h1`
  font-size: 1.6rem;
  margin-bottom: 1rem;
`;

const Skeleton = styled.div`
  background: #f5f5f5;
  min-width: 30rem;
  border-radius: 0.5rem;
  min-height: 20rem;
  display: flex;
  align-items: center;
  justify-content: center;
  flex-direction: column;
  h6 {
    font-size: 2.1rem;
  }
`;

const ImageDraw = styled.img`
  width: 100%;
  max-width: 14rem;
  height: auto;
  margin-bottom: 1rem;
  cursor: pointer;
`;

const getIdYoutube = url => {
  var regExp = /^.*((youtu.be\/)|(v\/)|(\/u\/\w\/)|(embed\/)|(watch\?))\??v?=?([^#&?]*).*/;
  var match = url.match(regExp);
  return match && match[7].length == 11 ? match[7] : false;
};

export default function Player({ title, id, height, width, type, purePlay }) {
  let url = "";
  if (id) {
    if (type == "youtube") {
      console.warn("===>", getIdYoutube(id));
      let idYT = getIdYoutube(id);
      url = `https://www.youtube.com/embed/${idYT}`;
    } else if (type == "twitchtv") {
      url = `https://player.twitch.tv/?channel=${id}&html5`;
    }
  }

  if (!id) {
    return (
      <Skeleton
        style={{
          width: width,
          height: height
        }}
      >
        <ImageDraw src="./images/player-boy-painel.svg" alt="beRapTV" />

        <h6>Pré-visualizar transmissão</h6>
      </Skeleton>
    );
  }

  return (
    <>
      {!purePlay ? <Title>{title}</Title> : false}
      <iframe
        width={width}
        height={height}
        src={url}
        frameBorder="0"
        allow="accelerometer; autoplay; encrypted-media; gyroscope; picture-in-picture"
        allowFullScreen
      />
    </>
  );
}
